import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';


class CardAside extends Component {
       constructor(props) {
              super(props);
              this.state = {
                     width: window.innerWidth,
                     isSavedList: false,
              };
              this.handleWindowResize = this.handleWindowResize.bind(this);
       }

       handleWindowResize() {
              this.setState({ width: window.innerWidth });
       }

       componentDidMount() {
              window.addEventListener('resize', this.handleWindowResize);
       }

       componentWillUnmount() {
              window.removeEventListener('resize', this.handleWindowResize);
       }

       handleClose = () => {
              this.props.filterToggleCallback(false)
       };
       render() {
              const { filterToggle, closeAside } = this.props
              const { width } = this.state
              return (
                     <div className={`aside__ border-end ${width < 1280 ? `offcanvas offcanvas-start ${filterToggle?'show':''}` : ''}`} id='aside__toggle'>
                            <div className='d-flex justify-content-end d-xl-none p-3 border-bottom h-48px'>
                                   <Button variant='secondary' id="toggle_aside" size='sm' className='d-flex border-0' onClick={()=>closeAside()}>
                                          <FontAwesomeIcon icon={'xmark'} iconClass={'me-0'}/>
                                   </Button>
                            </div>
                            <div className='aside__body w-100'>
                                   {
                                          this.props.children
                                   }
                            </div>
                     </div>
              );
       }
}

export default CardAside;