import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Form, Button, Card } from 'react-bootstrap';
import ColorPicker from '../../CommonComponents/ColorPicker';
import { hashtagHighlighter } from '../../../../../actions/appsMoreAction';
import { shopifyApp, shopifyForm, TOAST_DISMISS, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../utils';
import { useNavigate } from 'react-router-dom';
import { wallId } from '../../../../../utils/localstorage';

export default function HashHighlighter({ allWalls, getNewData }) {
  let activeWallData = allWalls?.find((x) => x.id == localStorage.getItem('wallId'))

  const [color, setColor] = useState(activeWallData?.hashtag_color || '');
  const [feedHashtag, setFeedHashtag] = useState(activeWallData?.hashtag_feed || false);
  const [allHashtag, setAllHashtag] = useState(activeWallData?.hashtag_all || false);
  const [status, setStatus] = useState(activeWallData?.hashtag_highlight || false);
  const [spinner, setSpinner] = useState(false);
  const [isChanges, setIsChanges] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    if(shopifyApp) {
      window.addEventListener('onSaveClicked',saveHighlight)
      window.addEventListener('onDiscardClicked',discardHighlight)
    }

    return () => {
      if(shopifyApp) {
        shopifyForm('hide')
        window.removeEventListener('onSaveClicked',saveHighlight)
        window.removeEventListener('onDiscardClicked',discardHighlight)
      }
    } 
  },[])

  useEffect(()=>{
    if(shopifyApp && isChanges) shopifyForm() 
  },[isChanges])

  const saveHighlight = () => onSubmitData()

  const discardHighlight = () => {
    setColor(activeWallData?.hashtag_color || '');
    setFeedHashtag(activeWallData?.hashtag_feed || false);
    setAllHashtag(activeWallData?.hashtag_all || false);
    setStatus(activeWallData?.hashtag_highlight || false);
    setIsChanges(false)
    shopifyForm('hide')
  }

  useEffect(() => {

    setColor(activeWallData?.hashtag_color || '');
    setFeedHashtag(activeWallData?.hashtag_feed || false);
    setAllHashtag(activeWallData?.hashtag_all || false);
    setStatus(activeWallData?.hashtag_highlight || false);
  }, [activeWallData]);

  const onSubmitData = (e) => {
    e.preventDefault();
    setSpinner(true);

    const data = {
      hashtag_color: color,
      hashtag_feed: `${Number(feedHashtag)}`,
      hashtag_all: `${Number(allHashtag)}`,
      hashtag_highlight: `${Number(status)}`,
    };

    hashtagHighlighter(data)
      .then(() => {
        if(shopifyApp) shopifyForm('hide')
        getNewData();
        TOAST_SUCCESS('Hashtag Highlighter saved successfully.');
        navigate(`/content/moderation/${wallId ? wallId : ''}`);
      })
      .catch(TOAST_ERROR)
      .finally(() => {
        setSpinner(false);
      });
  };

  const handleSwitchChange = (key, setValue) => {
    setValue((prevValue) => !prevValue);
    setIsChanges(true);
  };

  return (
    <Container className='pb-10 mb-10' fluid>
      <div className='my-5'>
        <Row>
          <Col lg={8} xl={6}>
            <Form.Group className='mb-10' controlId='HColor'>
              <Form.Label>Color</Form.Label>
              <ColorPicker id='HashtagColor' defaultColor={color} changeColor={(clr) => { setColor(clr); setIsChanges(true); }} />
            </Form.Group>
            <Row className='gx-10'>
              <Col xs={6}>
                <Form.Check type='switch' id='feedHashtag' className='f-between mb-5' label='Feed Hashtag' checked={feedHashtag} onChange={() => handleSwitchChange(feedHashtag, setFeedHashtag)} reverse />
              </Col>
              <Col xs={6}>
                <Form.Check type='switch' id='allHashtag' className='f-between mb-5' label='All Hashtag' checked={allHashtag} onChange={() => handleSwitchChange(allHashtag, setAllHashtag)} reverse />
              </Col>
              <Col xs={6}>
                <Form.Check type='switch' id='hashtagHighlight' className='f-between mb-5' label='Status' checked={status} onChange={() => handleSwitchChange(status, setStatus)} reverse />
              </Col>
            </Row>
          </Col>
        </Row>
        {!shopifyApp && <Card.Footer className='f-center justify-content-end position-absolute bottom-0 start-0 w-100 py-4 px-4 px-xl-6 bg-body' style={{zIndex:2}}>
          <Button type='submit' variant='primary' className={`btn-min-w ${spinner ? 'spinner' : ''}`} onClick={onSubmitData} disabled={!isChanges} >
            Apply
          </Button>
        </Card.Footer>}
      </div>
    </Container>
  );
}
