import React, { useEffect, useState, useRef } from 'react';
import { Container, Nav, Tab, ToggleButtonGroup, ToggleButton, Image, Form, Col, Row, Card, Button } from "react-bootstrap";
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { chooseLanguage } from '../../../../../actions/appsMoreAction';
import { shopifyApp, shopifyForm, TOAST_DISMISS, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../utils';
import { useNavigate } from 'react-router-dom';

export default function Language({ allWalls, getNewData }) {
  const [ChooseLang, setChooseLang] = useState();
  const [customLang, setCustomLang] = useState({ poweredBy: '', showMore: '', viewOn: '', all: '' });
  const [spinner, setSpinner] = useState(false);
  const [isCustomLang, setIsCustomLang] = useState(false);
  const [isLang, setIsLang] = useState(false);
  const [inputError, setInputError] = useState(false);
  const [activeKey, setActiveKey] = useState('language')
  const activeKeyRef = useRef(activeKey);

  const navigate = useNavigate();

  let activeWallData = allWalls?.find((x) => x.id == localStorage.getItem('wallId'))

  const language = [
    { lang: 'English', code: 'us' },
    { lang: 'French', code: 'fr' },
    { lang: 'Chinese', code: 'cn' },
    { lang: 'German', code: 'de' },
    { lang: 'Korean', code: 'kr' },
    { lang: 'Malay', code: 'my' },
    { lang: 'Italian', code: 'it' },
    { lang: 'Dutch', code: 'nl' },
    { lang: 'Portuguese', code: 'pt' },
    { lang: 'Spanish', code: 'de' }]

  useEffect(() => {
    setChooseLang(activeWallData?.w_language ? activeWallData?.w_language === 'custom' ? '' : activeWallData?.w_language : '');
    setCustomLang({ poweredBy: activeWallData?.custom_lan_data?.p_by || '', showMore: activeWallData?.custom_lan_data?.show_more || '', viewOn: activeWallData?.custom_lan_data?.view_on || '', all: activeWallData?.custom_lan_data?.p_all || '' });
  }, [activeWallData]);

  useEffect(() => {
    if(shopifyApp) {
      window.addEventListener('onSaveClicked',saveApp)
      window.addEventListener('onDiscardClicked',discardApp)
    }

    return () => {
      if(shopifyApp) {
        shopifyForm('hide')
        window.removeEventListener('onSaveClicked',saveApp)
        window.removeEventListener('onDiscardClicked',discardApp)
      }
    } 
  },[])

  useEffect(()=>{
    if(shopifyApp){
      if(activeKey == 'language' && isLang) shopifyForm()
      else if (activeKey == 'custom' && isCustomLang) shopifyForm()
    }
  },[isCustomLang, isLang])

  useEffect(() => {
    if(shopifyApp) activeKeyRef.current = activeKey;
  }, [activeKey]);

  const saveApp = () => {
    if(activeKeyRef.current == 'language') onSubmitData()
    else onSubmitCustomLanguage()
  }

  const discardApp = () => {
    setChooseLang('')
    setCustomLang({ poweredBy: '', showMore: '', viewOn: '', all: '' })
    setIsCustomLang(false)
    setIsLang(false)
    setInputError(false)
    shopifyForm('hide')
  }

  const handleSubmit = (data) => {
    setSpinner(true);
    chooseLanguage(data).then(() => {
      if(shopifyApp) shopifyForm('hide')
      getNewData();
      TOAST_DISMISS();
      TOAST_SUCCESS('Custom Language saved successfully.');
      navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`);
    }).catch(() => {
      TOAST_DISMISS();
      setSpinner(false);
      setIsLang(false)
      setIsCustomLang(false)
    });
  };

  const onSubmitData = () => ChooseLang ? handleSubmit({ lang: ChooseLang }) : TOAST_ERROR('Please select a language.');

  const onSubmitCustomLanguage = (e) => {
    e?.preventDefault();
    const { poweredBy, showMore, viewOn, all } = customLang;
    if (poweredBy && showMore && viewOn && all) {
      handleSubmit({ w_language: 'custom', p_by: poweredBy, show_more: showMore, view_on: viewOn, p_all: all });
    } else {
      setInputError(true)
    }
  };

  return (
    <Container fluid>
      <div className="my-5">
        <Tab.Container id="lang__" defaultActiveKey={activeKey} onSelect={event => setActiveKey(event)}>
          <Nav variant="underline" className="border-bottom mb-5">
            <Nav.Item><Nav.Link eventKey="language">Language</Nav.Link></Nav.Item>
            <Nav.Item><Nav.Link eventKey="custom">Custom</Nav.Link></Nav.Item>
          </Nav>
          <Tab.Content>
            <Tab.Pane eventKey="language">
              <ToggleButtonGroup type="radio" name="lang" className='flex-wrap' defaultValue={ChooseLang}>
                {language.map((item, index) => (
                  <ToggleButton key={index} variant={item.lang == ChooseLang ? 'primary' : 'secondary'} className={`mb-5 me-5`} value={item.lang} onClick={() => { setChooseLang(item.lang); setIsLang(true) }}>
                    <Image className='me-2' src={baseUrl(`media/images/flags/${item.code}.svg`)} height={17} width={28} fluid />
                    {item.lang}
                  </ToggleButton>
                ))}
              </ToggleButtonGroup>
              {!shopifyApp && <Card.Footer className="f-center justify-content-end position-absolute bottom-0 start-0 w-100 py-4 px-4 px-xl-6 bg-body">
                <Button type="submit" variant='primary' className={`btn-min-w ${spinner ? `spinner` : ''}`} onClick={() => onSubmitData()} disabled={!isLang}>Apply</Button>
              </Card.Footer>}
            </Tab.Pane>
            <Tab.Pane eventKey="custom">
              <Form onSubmit={onSubmitCustomLanguage}>
                <Row>
                  {['poweredBy', 'showMore', 'viewOn', 'all'].map((field, i) => (
                    <Form.Group key={i} className='mb-3' as={Col} md="6" controlId={field}>
                      <Form.Label>{field.replace(/([A-Z])/g, ' $1').trim()} <span className='text-danger'>*</span></Form.Label>
                      <Form.Control onChange={e => { setCustomLang({ ...customLang, [field]: e.target.value }); setIsCustomLang(true) }} value={customLang[field]} className={`${inputError && !customLang[field] ? `is-invalid` : ``}`} />
                      {inputError && !customLang[field] ? <div className="invalid-feedback">{field.replace(/([A-Z])/g, ' $1').trim()} is required </div> : null}
                    </Form.Group>
                  ))}
                </Row>
                {!shopifyApp && <Card.Footer className="f-center justify-content-end position-absolute bottom-0 start-0 w-100 py-4 px-4 px-xl-6 bg-body">
                  <Button type="submit" variant='primary' className={`btn-min-w ${spinner ? `spinner` : ''}`} disabled={!isCustomLang}>Apply</Button>
                </Card.Footer>}
              </Form>
            </Tab.Pane>
          </Tab.Content>
        </Tab.Container>
      </div>
    </Container>
  );
}